import React, { useEffect } from "react";
import cert from "./cert.module.css";
import sign from "../assets/signature.png";
import stamp from "../assets/stamp.png";
import Pdf from "react-to-pdf";
import { useSelector, useDispatch } from "react-redux";

const Certificate = ({ history }) => {
  const date = new Date().toISOString();
  const ref = React.createRef();
  const options = {
    orientation: "landscape",
    unit: "in",
    format: [8, 12],
  };

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (!userInfo) {
      history.push("/evaluation");
    }
    //setCurrentQuestion(questions && questions[0] && questions[0]);
  }, [history]);

  return (
    <div>
      <Pdf
        targetRef={ref}
        filename="Certificate.pdf"
        options={options}
        x={0.5}
        y={0.5}
        scale={0.8}
      >
        {({ toPdf }) => (
          <div className="col-md-6 mx-auto text-center py-2">
            <button onClick={toPdf} className="btn btn-danger">
              Download pdf
            </button>
          </div>
        )}
      </Pdf>
      <div className={cert.certificate} ref={ref}>
        <p className="text-center" style={{ color: "#640ad2" }}>
          <b>LOTUS BETA ANALYTICS NIGERIA LIMITED</b>
        </p>
        <br />
        <h1 className="text-center">CERTIFICATE</h1>
        <h5 className="text-center">OF PARTICIPATION</h5>

        <div className={cert.title}>
          <p className="text-center">
            THIS CERTIFICATE IS PROUDLY PRESENTED TO{" "}
          </p>
          <br />
          <h1 className="text-center">{userInfo && userInfo.name}</h1>
          <div style={{ padding: "0rem 10rem" }}>
            <hr />
          </div>
          <p className="text-center text-danger">
            FOR PARTICIAPTING IN THE LOTUS BETA SQL TRAINING
          </p>
        </div>
        <div className={`${cert.title} ${cert.padding}`}>
          <div className="col-md-8 mx-auto">
            <div className="row">
              <div className="col-md-4 float-left">
                <p className="text-center">{date.slice(0, 10)}</p>
                <hr />
                <p className="text-center">
                  <b>DATE</b>
                </p>
              </div>
              <div className="col-md-4 text-center float-right">
                <img src={stamp} alt="Seal" width="150px" />
              </div>
              <div className="col-md-4 text-center float-right">
                <img src={sign} alt="Ademola Akinbade" width="80px" />
                <hr />
                <p className="text-center">
                  <b>SIGNATURE</b>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Certificate;
