import React, { useState, useEffect } from "react";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import { addResponse, getQuestions } from "../actions/userActions";
import ParticlesComponent from "../components/ParticlesComponent";

const Evaluation = ({ history }) => {
  const dispatch = useDispatch();
  const [index, setIndex] = useState(0);
  const [testEnded, setTestEnded] = useState(false);
  const [response, setResponse] = useState("");
  const [comments, setComments] = useState("");
  const [msg, setMsg] = useState(false);

  const evaluation = useSelector((state) => state.evaluation);
  const { loading, error, questions } = evaluation;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    dispatch(getQuestions());
    if (!userInfo) {
      history.push("/evaluation");
    }
    //setCurrentQuestion(questions && questions[0] && questions[0]);
  }, [dispatch, history]);

  const nextQuestion = () => {
    //
    if (!response) {
      setMsg(true);
    } else {
      setMsg(false);

      const question = questions[index].question;
      const user = userInfo._id;
      const newIndex = index + 1;
      setIndex(newIndex);
      dispatch(addResponse(user, question, response, comments));
      setResponse("");

      if (newIndex >= questions.length) {
        setTestEnded(true);
        history.push("/certificate");
      }
    }
  };
  const prevQuestion = () => {
    //
    const newIndex = index - 1;
    setIndex(newIndex);
    if (newIndex >= questions.length) {
      setTestEnded(true);
    }
  };
  return (
    <>
      <ParticlesComponent />
      <div className={styles.app}>
        <div className={styles.contents}>
          {testEnded ? (
            <div>Completed</div>
          ) : (
            <div className="col-md-6 mx-auto" style={{ position: "relative" }}>
              <h4 className="text-success">
                <b>Hi {userInfo && userInfo.name}</b>
              </h4>
              <p>We would love to hear your feedback</p>
              {error && <div>{error}</div>}
              {loading && <h6>Loading...</h6>}
              {loading ? (
                "Fetching Questions..."
              ) : (
                <>
                  <h4>
                    {questions && questions[index] && questions[index].question}
                  </h4>
                  <div className="col-md-12 py-2">
                    {msg && (
                      <div className="alert alert-warning">
                        Your response is important to us, Please select an
                        answer
                      </div>
                    )}
                    {questions &&
                    questions[index] &&
                    questions[index].answer.length !== 0 ? (
                      <>
                        {questions &&
                          questions[index] &&
                          questions[index].answer.map((item, index) => (
                            <p key={index}>
                              <input
                                type="radio"
                                onChange={(e) => setResponse(item)}
                                name="response"
                                value={item}
                              />
                              &nbsp; {item}
                            </p>
                          ))}
                        <textarea
                          onChange={(e) => setComments(e.target.value)}
                          className="form-control"
                          rows={4}
                          value={comments}
                          placeholder="Any Comment"
                        ></textarea>
                      </>
                    ) : (
                      <textarea
                        onChange={(e) => setResponse(e.target.value)}
                        className="form-control"
                        rows={4}
                        value={response}
                      ></textarea>
                    )}
                  </div>
                  <div className="row">
                    {index !== 0 && (
                      <div className="col-md-4 py-2">
                        <button
                          className="btn form-control btn-dark"
                          onClick={prevQuestion}
                        >
                          Back
                        </button>
                      </div>
                    )}
                    <div className="col-md-4 py-2">
                      <button
                        className="btn form-control btn-success"
                        onClick={nextQuestion}
                      >
                        Next
                      </button>
                    </div>
                  </div>
                </>
              )}
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Evaluation;
