import React, { useState } from "react";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import { login } from "../actions/userActions";

const StartEvaluation = ({ history }) => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { loading, error, userInfo } = userLogin;

  const registerHandler = (e) => {
    e.preventDefault();
    dispatch(login(name, email));
  };

  if (userInfo) {
    history.push("/evaluation/begin");
  }

  return (
    <div className={styles.app}>
      <div className={styles.appGrid}>
        <div className={styles.background}>
          <div className={styles.overlay}>
            <h1>Lotus Beta Analytics</h1>
            <p>
              We are focused on the provision of Information Technology
              Solutions and Services that are built on a world-class, secure,
              resilient infrastructure, delivered according to global best
              practices in partnership with the world’s leading technology
              vendor
            </p>
            <br />
            <h2>Training:</h2>
            <h5>SQL Security</h5>
          </div>
        </div>
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          {loading ? (
            <div>Loadding.......</div>
          ) : (
            <>
              <div className="text-center">
                <h5>Training Evaluation</h5>
              </div>
              <div className={styles.forms}>
                <form onSubmit={registerHandler}>
                  <div className={styles.formContainer}>
                    <label>Name</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setName(e.target.value)}
                    />
                  </div>

                  <div className={styles.formContainer}>
                    <label>Email</label>
                    <input
                      type="email"
                      className="form-control"
                      onChange={(e) => setEmail(e.target.value)}
                      value={email}
                    />
                  </div>
                  <div className={styles.formContainer}>
                    <input
                      type="submit"
                      className="form-control btn btn-dark"
                      value="Start Evaluation"
                    />
                  </div>
                </form>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default StartEvaluation;
