import {
  USER_REGISTER_REQUEST,
  USER_REGISTER_SUCCESS,
  USER_REGISTER_FAIL,
  USER_REGISTER_RESET,
  GET_QUESTIONS_FAIL,
  GET_QUESTIONS_REQUEST,
  GET_QUESTIONS_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  ADD_RESPONSE_FAIL,
  ADD_RESPONSE_REQUEST,
  ADD_RESPONSE_SUCCESS,
} from "../constants/userConstants";

export const userRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REGISTER_REQUEST:
      return { loading: true };
    case USER_REGISTER_SUCCESS:
      return { loading: false, success: true, register: action.payload };
    case USER_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case USER_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const getQuestionReducer = (state = { questions: [] }, action) => {
  switch (action.type) {
    case GET_QUESTIONS_REQUEST:
      return { loading: true };
    case GET_QUESTIONS_SUCCESS:
      return { loading: false, success: true, questions: action.payload.data };
    case GET_QUESTIONS_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return { loading: true };
    case USER_LOGIN_SUCCESS:
      return { loading: false, success: true, userInfo: action.payload.data };
    case USER_LOGIN_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const userResponseReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_RESPONSE_REQUEST:
      return { loading: true };
    case ADD_RESPONSE_SUCCESS:
      return { loading: false, success: true };
    case ADD_RESPONSE_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
