import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Certificate from "./screen/Certificate";
import Evaluation from "./screen/Evaluation";
import HomeScreen from "./screen/HomeScreen";
import StartEvaluation from "./screen/StartEvaluation";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={HomeScreen} />
        <Route path="/evaluation" exact component={StartEvaluation} />
        <Route path="/evaluation/begin" exact component={Evaluation} />
        <Route path="/certificate" exact component={Certificate} />
      </Switch>
    </Router>
  );
}

export default App;
