import axios from "axios";
import {
  USER_REGISTER_REQUEST,
  USER_REGISTER_SUCCESS,
  USER_REGISTER_FAIL,
  GET_QUESTIONS_SUCCESS,
  GET_QUESTIONS_REQUEST,
  GET_QUESTIONS_FAIL,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_REQUEST,
  USER_LOGIN_FAIL,
  ADD_RESPONSE_SUCCESS,
  ADD_RESPONSE_REQUEST,
  ADD_RESPONSE_FAIL,
} from "../constants/userConstants";

export const registerUser =
  (firstname, lastname, email, mobile, department) => async (dispatch) => {
    try {
      dispatch({ type: USER_REGISTER_REQUEST });

      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const { data } = await axios.post(
        "/api/v1/training/",
        { firstname, lastname, email, mobile, department },
        config
      );

      dispatch({
        type: USER_REGISTER_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: USER_REGISTER_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      });
    }
  };

export const getQuestions = () => async (dispatch) => {
  try {
    dispatch({ type: GET_QUESTIONS_REQUEST });

    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const { data } = await axios.get("/api/v1/evaluation/", {}, config);

    dispatch({
      type: GET_QUESTIONS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_QUESTIONS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

